.personaje-thumbnail {
  position: relative;
  width: 60px;
  max-height: 60px;
  margin: 10px;
  border-radius: 10px;
  overflow: hidden;
  background-color: #42363D;

  display: flex;
  justify-content: center; /* Centrado horizontal */
  align-items: flex-start; /* Alineación superior */
}

.personaje-thumbnail img {
  width: 200%;
  object-fit: cover;
}

.other > img{
  width: 100%;
  height: 100%;
  background-color: transparent;
}

